<template>
  <div>
    <!-- Note that 'ref' is important here (value can be anything). read the description about `ref` below. -->
    <vue-croppie
      ref="croppieRef"
      :enableOrientation="true"
      :boundary="{ width: 300, height: 300 }"
      @result="result"
      @update="update"
    >
    </vue-croppie>

    <!-- the result -->
    <div class="post">
      <img class="post-image" v-bind:src="cropped" />
      <img class="post-poloroid" src="../assets/poloroid.png" />
    </div>

    <button @click="bind()">Bind</button>
    <!-- Rotate angle is Number -->
    <button @click="rotate(-90)">Rotate Left</button>
    <button @click="rotate(90)">Rotate Right</button>
    <button @click="crop()">Crop Via Callback</button>
    <button @click="cropViaEvent()">Crop Via Event</button>
  </div>
</template>

<script>
const IMG_URL_KEY = "imgUrl";
export default {
  name: "Test",
  mounted() {
    // Upon mounting of the component, we accessed the .bind({...})
    // function to put an initial image on the canvas.
    let uri = window.location.search.substring(1);
    let params = new URLSearchParams(uri);
    console.log(params.get(IMG_URL_KEY));
    let url = params.get(IMG_URL_KEY)
      ? params.get(IMG_URL_KEY)
      : "http://i.imgur.com/Fq2DMeH.jpg";

    this.$refs.croppieRef.bind({
      url,
    });
  },
  data() {
    return {
      cropped: null,
      images: [
        "http://i.imgur.com/fHNtPXX.jpg",
        "http://i.imgur.com/ecMUngU.jpg",
        "http://i.imgur.com/7oO6zrh.jpg",
        "http://i.imgur.com/miVkBH2.jpg",
      ],
    };
  },
  methods: {
    bind() {
      // Randomize cat photos, nothing special here.
      let url = this.images[Math.floor(Math.random() * 4)];

      // Just like what we did with .bind({...}) on
      // the mounted() function above.
      this.$refs.croppieRef.bind({
        url: url,
      });
    },
    // CALBACK USAGE
    crop() {
      // Here we are getting the result via callback function
      // and set the result to this.cropped which is being
      // used to display the result above.
      let options = {
        format: "jpeg",
        circle: true,
      };
      this.$refs.croppieRef.result(options, (output) => {
        this.cropped = output;
      });
    },
    // EVENT USAGE
    cropViaEvent() {
      let options = {
        format: "jpeg",
      };
      this.$refs.croppieRef.result(options);
    },
    result(output) {
      this.cropped = output;
    },
    update(val) {
      console.log(val);
    },
    rotate(rotationAngle) {
      // Rotates the image
      this.$refs.croppieRef.rotate(rotationAngle);
    },
  },
};
</script>

