  width: 350px;
  height: 350px;
  background: red;
  position: relative;
}
.post-image {
  position: absolute;
  top: 40%;
  width: 61%;
  left: 50%;
  transform: translate(-50%, -44%);
}
.post-poloroid {
  position: absolute;
  width: 120%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
